/*新闻*/

.newsListSecondRow{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    margin-top: 15px;
}

.newsListIcon{
    width: 18px;
}

.newsListItemOthers{
    color: #999999;
    font-size: 14px;
    font-weight: 300;
    text-align: center;
    margin-left: 3px;
}

.newsListItemLongText{
    overflow : hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    text-align: left;
    margin-left: 0px;
    line-height: 17px;
    height: 34px;
    margin: 5px 0 5px 0;
}

.newsListItemTextBox{
    margin-left: 10px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex: 1.5 1;
        flex: 1.5 1;
    -ms-flex-pack: justify;
        justify-content: space-between;
    min-height: 85px;
}

.newsDetailHeaderRow{
    padding: 10px;
}

.newsDetailHeaderName{
    text-align: center;
    color: #333333;
    font-weight: 400;
}

.newsDetailSplitLine{
    height: 1px;
    border-bottom: 1px #dddddd solid;
    margin-bottom: 5px;
}

.newsDetailHtml{
    padding-left: 10px;
    padding-right: 10px;
    font-size: 14px;
    font-weight: 300;
    line-height: 25px;
}

.newsDetailHtml div{
    max-width: 100% !important;
}

.newsDetailHtml img{
    width: 100% !important;
    height: auto !important;
}

.newsListItem{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    padding: 10px;
}

.newsListItemSplit{
    border-bottom: 1px #dddddd solid;
}


