/*
* 地图
*/

.distanceTimeBox
{
    display:-ms-flexbox;
    display:flex;
    display: -webkit-flex;
    -ms-flex-direction: row;
        flex-direction: row;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 35px;
    background-color: rgba(255, 255, 255, 0.9);
    -webkit-box-shadow: 0px 3px 5px #bbb;
            box-shadow: 0px 3px 5px #bbb;
    z-index: 10000;
}
.distanceTimeBoxItem
{
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-item-align: center;
        align-self: center;
    text-align: center;
    font-size: 14px;
    color: #333333;
}
.navWay
{
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 40px;
    background-color: rgba(255, 255, 255, 0.9);
    -webkit-box-shadow: 0px -3px 5px #bbb;
            box-shadow: 0px -3px 5px #bbb;
    -ms-flex-item-align: center;
        align-self: center;
    text-align: center;
    line-height: 40px;
    font-size: 14px;
    color: #333333;
}

.navResultPanel
{
    position: absolute;
    left: 0;
    top: 35px;
    right: 0;
    bottom: 40px;
    background-color: rgba(0, 0, 0, 0.3);
}
.navpanel-enter {
    opacity: 0.01;
}

.navpanel-enter .weui-page{
    left: 100%;
    -webkit-transition: left 500ms ease-in;
    -o-transition: left 500ms ease-in;
    transition: left 500ms ease-in;
}

.navpanel-enter.navpanel-enter-active {
    opacity: 1;
    -webkit-transition: opacity 500ms ease-in;
    -o-transition: opacity 500ms ease-in;
    transition: opacity 500ms ease-in;
}

.navpanel-leave {
    opacity: 1;
}

.navpanel-leave.navpanel-leave-active {
    opacity: 0.01;
    -webkit-transition: opacity 300ms ease-in;
    -o-transition: opacity 300ms ease-in;
    transition: opacity 300ms ease-in;
}

.navpanel-leave.navpanel-leave-active .weui-page{
    left: 100%;
    -webkit-transition: left 300ms ease-in;
    -o-transition: left 300ms ease-in;
    transition: left 300ms ease-in;
}

div.navtrans-view.expand
{
    display: none;
}

/** 类型地图 中 nav 中 flex 设置 
*/
.elementMapPage .am-navbar-left{
    -ms-flex: 1 1;
        flex: 1 1
}
.elementMapPage .am-navbar-title{
    -ms-flex: 6 1;
        flex: 6 1
}
.elementMapPage .am-navbar-right{
    -ms-flex: 1 1;
        flex: 1 1
}
.elementMapPage .am-search{
    width: 100%;
    background-color: rgba(0,0,0,0);
}
.elementMapPage .am-search-cancel{
    color: white
}

.mapTypePanel
{
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.3);
    overflow: hidden;
}
.elementMapPanel-enter {
    opacity: 0.01;
}

.elementMapPanel-enter .weui-page{
    bottom: 100%;
    -webkit-transition: bottom 500ms ease-in;
    -o-transition: bottom 500ms ease-in;
    transition: bottom 500ms ease-in;
}

.elementMapPanel-enter.elementMapPanel-enter-active {
    opacity: 1;
    -webkit-transition: opacity 500ms ease-in;
    -o-transition: opacity 500ms ease-in;
    transition: opacity 500ms ease-in;
}

.elementMapPanel-leave {
    opacity: 1;
}

.elementMapPanel-leave.elementMapPanel-leave-active {
    opacity: 0.01;
    -webkit-transition: opacity 300ms ease-in;
    -o-transition: opacity 300ms ease-in;
    transition: opacity 300ms ease-in;
}

.elementMapPanel-leave.elementMapPanel-leave-active .weui-page{
    bottom: 100%;
    -webkit-transition: bottom 300ms ease-in;
    -o-transition: bottom 300ms ease-in;
    transition: bottom 300ms ease-in;
}

.elementMapPage .footerDetails{
    position: absolute;
    left: 12px;
    right: 12px;
    bottom: 12px;
    background-color: white;
    border: 0.5px solid #e5e5e5;
}
.elementMapPage .footerDetailsName{
    font-size: 14px;
    margin: 5px 0 5px 12px;
    color: #333333;
}

.elementMapPage .footerDetailsAddress{
    font-size: 13px;
    color: #999999;
    margin: 0 5px 5px 12px;
}

.elementMapPage .footerDetailsButtons{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    border-top: 0.5px solid #e5e5e5;
}

.elementMapPage .footerDetailsButton{
    -ms-flex: 1 1;
        flex: 1 1;
    height: 40px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    font-size: 13px;
    color: #333333;
}

.elementMapPage .footerDetailsButtonDisabled{
    color: #a2a2a2;
}

.elementMapPage .footerDetailsButtonDisabled img{
    -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
    filter: grayscale(100%);
}

.elementMapPage .footerDetailsButton img{
    width: 15px;
    height: 15px;
    margin-right: 3px;
}
.elementMapPage .footerDetailsSplitLine{
    border-right: #e5e5e5 1px solid;
}

.elementMapPage .typeBoxView{
    margin-bottom: 20px;
    margin-top: 80px;
}

.elementMapPage .searchTypeName{
    position: absolute;
    left: 15px;
    top: 20px;
    color: white;
    background-color: #3dbf74;
    font-size: 14px;
    line-height: 30px;
    text-align: center;
    border-radius: 3px;
    width: calc((100% - 60px)/3);
}

.elementMapPage .typeView{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: start;
        align-items: flex-start;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.elementMapPage .typeButton{
    width: calc( ( 100% - 60px ) /3 );
    text-align: center;
    border-radius: 3px;
    color: #999999;
    margin-left: 15px;
    margin-top: 15px;
    font-size: 14px;
    line-height: 30px;
    border: 0.5px solid #e5e5e5;
}

.elementMapPage .typeName{
    color: #999999;
    margin-left: 15px;
    font-size: 16px;
}


