.pictureBox{
    padding: 10px 10px 0;
    background-color: #666;
}

.pictureItem{
    display: block;
    position: relative;
}

.pictureItemBg{
    width: 100%;
    height: auto;
    margin-bottom: 10px;
    vertical-align: middle;
    border-radius: 3px;
    -webkit-box-shadow: 0 2px 5px rgba(0,0,0,.2);
            box-shadow: 0 2px 5px rgba(0,0,0,.2);
}

.pictureItemText{
    position: absolute;
    width: 200px;
    font-size: 18px;
    top: 40%;
    text-align: center;
    border: 1px solid #eee;
    left: 50%;
    margin-left: -100px;
    background-color: rgba(0,0,0,0.15);
    padding: 5px 0;
}

.pictureItemTextTit{
    font-size: 28px;
    color: #fff;
}

.picturePage .react-image-viewer{
    position: fixed !important;
    top: 0;
    background-color: black;
    z-index: 100;
}

.picturePage .react-image-viewer .tool-bar{
    display: none;
}

.pictureClose{
    width: 40px;
    height: 40px;
    position: fixed;
    top: 20px;
    z-index: 100;
    left: calc(50vw - 20px);
}

.photoBrowser{
    position: fixed;
    top: 0; bottom: 0; left: 0; right: 0;
    z-index: 9999;
    width: 100%;
    height: 100%;
    background-color: 'transparent';
}

.photoBrowserOpen{
    display: block;
}

.photoBrowserClose{
    display: none;
}


