.overviewListLastItem{
    margin-bottom: 10px;
}

.overviewListItem{
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 10px;
    height: 200px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.overviewDetail{
    margin: 10px;
    background-color: white;
    font-weight: 300;
    padding-bottom: 10px;
    border-radius: 2px;
    -webkit-box-shadow: 0 1px 2px rgba(0,0,0,.3);
            box-shadow: 0 1px 2px rgba(0,0,0,.3);
}

.overviewDetailImage{
    width: 100%;
}

.overviewDetailAuthor{
    font-size: 14px;
    color: #8e8e93;
    margin: 10px 10px 0 10px;
}

.overviewDetailTitle{
    font-size: 16px;
    color: #9c001a;
    font-weight: 500;
    margin: 10px 10px 0 10px;
}

.overviewDetailSubTitle{
    font-size: 14px;
    color: #825656;
    font-weight: 400;
    margin: 10px 10px 0 10px;
}

.overviewDetailContent{
    font-size: 14px;
    color: #333333;
    line-height: 25px;
    margin: 10px 10px 0 10px;
    text-indent: 28px;
    font-weight: 340;
}


