/**酒店**/

.hotelRecommendListBottomText{
    color: white;
    font-size: 17px;
    font-weight: 300;
    margin-left: 15px;
    margin-bottom: 13px;
    margin-right: 10px;
    -ms-flex: 1 1;
        flex: 1 1;
    white-space: nowrap;
    -o-text-overflow:ellipsis;
       text-overflow:ellipsis;
    overflow: hidden;
}

.hotelRecommendListBottomStars{
    margin-right: 15px;
    margin-bottom: 10px;
}

.hotelDetailDateRow{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    background-color: #808993;
}

.hotelDetailDateItem{
    -ms-flex: 1 1;
        flex: 1 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.hotelDetailDateSplit{
    width: 1px;
    margin: 15px 0 15px 0;
    background-color: #a6adb4;
}

.hotelDetailDateItemLabel{
    font-size: 13px;
    font-weight: 300;
    margin: 8px 0 0 0;
    color: #c9ced3;
}

.hotelDetailDateItemValue{
    font-size: 15px;
    font-weight: 300;
    margin: 8px 0 8px 0;
    color: white;
}

.hotelDetailRoomItemRow{
    font-size: 14px;
    font-weight: 300;
    background-color: #dee3e8;
    height: 44px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 10px 0 10px;
}

.hotelDetailRoomItemName{
    color: #333333;
}

.hotelDetailRoomItemPrice{
    color: #f48225;
}


