/*个人中心*/

.loginBottomText{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    color: #333333;
    font-size: 15px;
}
.loginBottomTextLeft{
    padding: 10px;
}
.loginBottomTextRight{
    padding: 10px;
}
.loginInput{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
    position: relative;
}
.inputPhone{
    padding: 15px 0px 15px 0px;
    border-width: 0px;
    width: 100%;
}
.inputPwd{
    padding: 15px 0px 15px 0px;
    border-width: 0px;
    -ms-flex: 1 1;
        flex: 1 1;
}
.loginSendCode{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    border-width: 0px;
    border-bottom: 0.5px solid #ddd;
}

.logoLogin{
    display: -ms-flexbox;
    display: flex;
    position: absolute;
    width: 100%;
    bottom: 20px;
    -ms-flex-pack: center;
        justify-content: center;
}

.am-button-primary::before{
    border-color:#58c0e7 !important;
}

.am-button-ghost.am-button-disabled::before{
    border-color:#999999 !important;
}

.am-navbar-light{
    color: #58c0e7;
}

.am-button-ghost.am-button-disabled{
    color: #999999;
}

.am-button-ghost{
    color: #58c0e7;
}
.am-button-ghost::before{
    color: #58c0e7;
}

.am-list-item.am-input-item {
    -ms-flex: 1 1;
        flex: 1 1;
}
.findPasswordContent{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin-top: 40px;
}
.findPasswordInput{
    display: -ms-flexbox;
    display: flex;
    margin: 20px 10px 10px 10px;
    padding-bottom: 10px;
    border-bottom: 0.5px solid #dddddd;
    font-size: 15px;
    color: #333333;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    position: relative;
}
.findPasswordInputInfo{
    color: #333333;
    font-size: 15px;
    -ms-flex-item-align: center;
        align-self: center;
    width: 60px;
}
.findPasswordInputPhone{
    border-width: 0px;
    margin-left: 10px;
}
.am-button{
    font-size: 14px;
}
.sendCodeButton{
    font-size: 14px;
    color: #58c0e7;
    border: 1px solid #58c0e7;
    border-radius: 3px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    margin: 5px;
    padding: 5px;
}

.inputPhoneDiv{
    position: relative;
    border-bottom: 0.5px solid #dddddd;  
    padding-bottom: 5px;  
}


