.weatherPage .navBar{
    background-color: transparent !important;
}

.weatherPage .appContent{
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.weatherBox{
    text-align: center;
    color: white;
    font-weight: 300;
}

.weatherToday{
    position: absolute;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    top: 17%;
}

.weatherTodayIcon{
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 150px;
    height: 150px;
}

.weatherTodayTemp{
    font-size: 18px;
    color: #fff;
    margin-top: 12px;
}

.weatherThreeDay{
    background-color: rgba(0,0,0,0.3);
    position: absolute;
    width: 100%;
    bottom: 0;
    height: 40%;
}

.weatherSituation{
    text-align: center;
    font-size: 15px;
    color: #fff;
    padding: 17px 0;
}

.weatherSituation span{
    border-left: 1px solid #c6c6c6;
    margin-left: 8px;
    padding-left: 7px;
    font-size: 13px;
    color: #c6c6c6;
}

.weatherThreeDayRow{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-align: start;
        align-items: flex-start;
}

.weatherThreeDayRowItem{
    -ms-flex: 1 1;
        flex: 1 1;
    border-right: 1px solid #555;
}

.weatherThreeDayRowItemDate{
    font-size: 15px;
    text-transform: uppercase;
    margin-bottom: 4px;
}

.weatherThreeDayRowItemIcon{
    background-position: center;
    background-repeat: no-repeat;
    background-size: 60%;
    width: 80px;
    height: 80px;
    margin: auto;
}

.weatherThreeDayRowItemTemp{
    font-size: 14px;
    margin-bottom: 5px;
}

.weatherThreeDayRowItemQuality{
    font-size: 11px;
}


