.ticketPurchaseBox{
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    background-color: #97def2;
}

.ticketPurchaseBanner{
    width: 100%;
    height: auto;
}

.ticketPurchaseContent{
    margin: 0 15px;
    background-color: white;
    padding: 5px 10px 10px 10px;
    position: relative;
}

.ticketPurchaseContentEndTime{
    background-color: #e5e8ea;
    color: #4d4d4d;
    font-size: 0.8em;
    padding: 10px;
    line-height:0.8em;
    text-align: right; 
}

.ticketPurchaseContentEndTimeSpan{
    margin:0 3px;
    padding: 2px;
    color: white;
    background-color: #242424;
}

.ticketPurchaseContentStartTime{
    position: absolute;
    top: 0;
    left: 15px;
    background-image: url('../../assets/images/ticketPurchase/time_bg.png');
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 45px;
    text-align: center;
    z-index: 100;
}

.ticketPurchaseContentStartTime .daytime{
    color: #fff;
    font-size: 0.8em;
    font-weight: 300;
    margin-top: 4px;
}

.ticketPurchaseContentStartTime .hourtime{
    color: #f7f874;
    font-size: 1em;
    font-weight: 300;
}
.ticketPurchaseContentImgBox{
    position: relative;
}

.ticketPurchaseContentImgBox .ticketimgBg{
    width: 100%;
}

.ticketPurchaseContentImgBox .ticketContent{
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 30px;
    line-height: 30px;
    background-color: rgba(0,0,0,0.7);
    display: -ms-flexbox;
    display: flex;
    color: white;
}

.ticketPurchaseContentImgBox .ticketContent .ticketName{
    font-size: 1.1em;
    margin-left: 10px;
    -ms-flex: 1 1;
        flex: 1 1
}

.ticketPurchaseContentImgBox .ticketContent .leftNumber{
    font-size: 1.1em;
    margin-right: 10px;
}

.ticketPurchaseContentBuy{
    background-color: #fa5a49;
    padding: 10px;
    display: -ms-flexbox;
    display: flex;
}

.ticketPurchaseContentBuy .ticketPrice{
    -ms-flex: 1 1;
        flex: 1 1;
    line-height: 1.7em;
}

.ticketPurchaseContentBuy .ticketPrice .salePrice{
    font-size: 1.3em;
    color: #feec57;
}

.ticketPurchaseContentBuy .ticketPrice .normalPrice{
    text-decoration: line-through;
    color: #a31708;
    font-size: 0.8em;
}

.ticketPurchaseContentBuy .buyBtn{
    line-height: 1.7em;
    color: #ea3a27;
}

.ticketPurchaseRole{
    margin: 10px 15px;
}

.ticketPurchaseRoleTip{
    color: #1e9abd;
    text-align: center;
    font-size: 1.3em;
    margin-bottom: 10px;
}

.ticketPurchaseList{
    margin: 10px 15px;
}

.ticketPurchaseListTip{
    width: 100%;
}

.ticketPurchaseListContent{
    padding: 10px 5px 0px 6px;
    background-size: 100%;
    margin-top: 15px;
    background-repeat: no-repeat;
}

.ticketPurchaseListUl{
    background-color: #c9f1fc;
    padding-top: 5px;
    height: 200px;
    overflow-y: scroll;
    margin: 0;
    padding: 0;
}

.ticketPurchaseListBottom{
    width: 100%;
    background-repeat: no-repeat;
    background-size: 100% auto;
    min-height: 80px;
}

.ticketPurchaseListItem{
    overflow: hidden;
    margin: 4% 6%;
    padding-bottom: 4%;
    border-bottom: solid 1px #97e1d4;
    line-height: 25px;
}

.ticketPurchaseItemHead{
    width: 25px;
    height: 25px;
    overflow: hidden;
    float: left;
}

.ticketPurchaseItemContent{
    display: inline;
    color: #333;
    padding-left: 10px;
    font-size: 14px;
}

.ticketPurchaseItemSpan{
    color: #fd4e3b;
    float: right;
}


