/*线路*/

.lineListItemName{
    color: #333333;
    font-weight: 400;
    font-size: 15px;
}

.lineListTravelName{
    font-size: 14px;
    color: #ff9500;
    margin-top: 8px;
    margin-bottom: 8px;
}
.lineDetailHeaderBottomText{
    position: absolute;
    right: 0;
    bottom: 10px;
    color: white;
    font-size: 18px;
    font-weight: 300;
    margin-left: 15px;
    /* margin-bottom: 10px; */
    margin-right: 15px;

}

.lineListTrafficDayRow{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.lineDetailSplitLine{
    height: 1px;
    margin-top: 15px;
    border-bottom: 1px #dddddd solid;
}

.lineDetailInfoLabel
{
    color: #6d7984;
    margin-left: 15px;
    margin-bottom: 10px;
}

.lineDetailArrangeListBox{
    margin: 15px 15px 15px 0px;
}

.lineArrangeItemDay{
    color: white;
    background: #9fc8f0;
    text-align: center;
    height: 28px;
    line-height: 28px;
    width: 70px;
    border-top-right-radius: 14px;
    border-bottom-right-radius: 14px;
    font-size: 15px;
    font-weight: 400;
    letter-spacing: 3px;
}

.lineArrangeItemDayBox{
    margin-left: 35px;
    border-left: 1px solid #dee3e8;
}

.lineArrangeItemDayTitleBox{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.lineArrangeItemDayTitle{
    height: 28px;
    line-height: 28px;
    padding: 0 10px;
    background: #eee;
    border-radius: 14px;
    -webkit-border-radius: 14px;
    -moz-border-radius: 14px;
    font-weight: 300;
    color: #788ea3;
}

.lineArrangeItemDayIcon{
    width: 24px;
    height: 24px;
    background: url(../images/line/line_scnic-hotel.png) no-repeat;
    background-size: 100%;
    margin-left: -12px;
    margin-right: 15px;
    margin-top: 2px;
    display: inline-block;
    vertical-align: bottom;
}

.lineArrangeItemDayAreaNameIcon{
    background-position: 0 0;
}

.lineArrangeItemDayHotelNameIcon{
    background-position: 0 100%;
}

.lineArrangeItemSpace{
    height: 10px;
}

.lineArrangeItemDayDetails{
    margin-left: 37px;
    color: #6d7984;
    font-weight: 300;
}

.lineArrangeItemDayTravelTimeBox{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
}

.lineArrangeItemDayTravelTimeIcon{
    width: 5px;
    height: 5px;
    background: #dee3e8;
    border-radius: 50%;
    -moz-border-radius: 50%;
    -webkit-border-radius: 50%;
    margin-left: -3.5px;
}

.lineArrangeItemDayTravelTime{
    margin-left: 34px;
    color: #333333;
    font-weight: 300;
}

.lineDetailOtherViewsSpace{
    background-color: #efeff4;
    height: 8px;
}

.lineArrangeItemDayLongDetails{
    line-height: 25px;
}


